"use client";

import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  Play,
  Pause,
  RotateCcw,
  Coffee,
  Brain,
  Armchair,
  Timer,
  History,
  Maximize2,
  Minimize2,
  CheckCircle2,
  Save,
  Settings2,
  X,
} from "lucide-react";
import { usePomodoro } from "@/lib/db/usePomodoro";
import { format } from "date-fns";

// --- TYPES ---
type Mode = "work" | "short" | "long";

interface TimerSettings {
  work: number;
  short: number;
  long: number;
}

// --- CONFIG ---
const MODE_CONFIG = {
  work: {
    label: "Deep Focus",
    color: "text-indigo-500",
    bg: "bg-indigo-500",
    lightBg: "bg-indigo-50",
    border: "border-indigo-200",
    gradient: "from-indigo-500 to-purple-600",
  },
  short: {
    label: "Short Break",
    color: "text-teal-500",
    bg: "bg-teal-500",
    lightBg: "bg-teal-50",
    border: "border-teal-200",
    gradient: "from-teal-400 to-emerald-500",
  },
  long: {
    label: "Long Break",
    color: "text-blue-500",
    bg: "bg-blue-500",
    lightBg: "bg-blue-50",
    border: "border-blue-200",
    gradient: "from-blue-400 to-cyan-500",
  },
};

export default function PomodoroPage() {
  const { addSession, todaySessions } = usePomodoro();

  // --- STATE ---
  const [mode, setMode] = useState<Mode>("work");
  // Default Settings
  const [settings, setSettings] = useState<TimerSettings>({
    work: 25,
    short: 5,
    long: 15,
  });

  const [timeLeft, setTimeLeft] = useState(settings.work * 60);
  const [isActive, setIsActive] = useState(false);
  const [isZenMode, setIsZenMode] = useState(false);
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);

  // Refs
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const timerInterval = useRef<NodeJS.Timeout | null>(null);
  const initialTimeRef = useRef(settings.work * 60); // To track duration for "Stop & Save"

  // --- 1. INITIALIZATION & PERSISTENCE ---
  useEffect(() => {
    audioRef.current = new Audio(
      "https://actions.google.com/sounds/v1/alarms/beep_short.ogg"
    );

    // Load saved settings from local storage if available
    const savedSettings = localStorage.getItem("pomodoro_settings");
    if (savedSettings) {
      const parsed = JSON.parse(savedSettings);
      setSettings(parsed);
      setTimeLeft(parsed.work * 60);
      initialTimeRef.current = parsed.work * 60;
    }

    // Check for running timer persistence
    const savedTarget = localStorage.getItem("pomodoro_target_time");
    const savedMode = localStorage.getItem("pomodoro_mode") as Mode;

    if (savedTarget && savedMode) {
      const targetTime = parseInt(savedTarget, 10);
      const now = Date.now();
      const diff = Math.ceil((targetTime - now) / 1000);

      if (diff > 0) {
        setMode(savedMode);
        setTimeLeft(diff);
        setIsActive(true);
        // Update initial time ref based on current settings for that mode
        const loadedSettings = savedSettings
          ? JSON.parse(savedSettings)
          : settings;
        initialTimeRef.current = loadedSettings[savedMode] * 60;
      } else {
        localStorage.removeItem("pomodoro_target_time");
        localStorage.removeItem("pomodoro_mode");
        setMode(savedMode);
        setTimeLeft(0);
      }
    }
  }, []);

  // --- 2. TIMER LOGIC ---
  useEffect(() => {
    if (isActive) {
      if (!localStorage.getItem("pomodoro_target_time")) {
        const targetTime = Date.now() + timeLeft * 1000;
        localStorage.setItem("pomodoro_target_time", targetTime.toString());
        localStorage.setItem("pomodoro_mode", mode);
      }

      timerInterval.current = setInterval(() => {
        const targetTime = parseInt(
          localStorage.getItem("pomodoro_target_time") || "0",
          10
        );
        const now = Date.now();
        const diff = Math.ceil((targetTime - now) / 1000);

        if (diff <= 0) {
          handleTimerComplete();
        } else {
          setTimeLeft(diff);
          document.title = `${formatTime(diff)} - ${MODE_CONFIG[mode].label}`;
        }
      }, 1000);
    } else {
      if (timerInterval.current) clearInterval(timerInterval.current);
      localStorage.removeItem("pomodoro_target_time");
      document.title = "TaskGlyph - Focus";
    }

    return () => {
      if (timerInterval.current) clearInterval(timerInterval.current);
    };
  }, [isActive, mode]);

  // --- HANDLERS ---

  const handleTimerComplete = () => {
    setIsActive(false);
    setTimeLeft(0);
    localStorage.removeItem("pomodoro_target_time");
    audioRef.current?.play().catch((e) => console.log("Audio play failed", e));

    // Full session completed
    const duration = settings[mode];
    addSession(duration, mode === "work" ? "work" : "break");

    if (mode === "work") {
      if (confirm("Focus session complete! Start a short break?")) {
        switchMode("short");
      }
    } else {
      if (confirm("Break over! Ready to focus?")) {
        switchMode("work");
      }
    }
  };

  const handleStopAndSave = () => {
    if (!isActive && timeLeft === initialTimeRef.current) return; // Hasn't started

    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");

    const timeSpentSeconds = initialTimeRef.current - timeLeft;
    const timeSpentMinutes = Math.floor(timeSpentSeconds / 60);

    if (timeSpentMinutes >= 1) {
      if (
        confirm(
          `You focused for ${timeSpentMinutes} minutes. Save this session?`
        )
      ) {
        addSession(timeSpentMinutes, mode === "work" ? "work" : "break");
      }
    }

    // Reset timer
    setTimeLeft(settings[mode] * 60);
  };

  const toggleTimer = () => {
    if (!isActive && timeLeft === 0) {
      setTimeLeft(settings[mode] * 60);
    }
    setIsActive(!isActive);
  };

  const resetTimer = () => {
    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");
    setTimeLeft(settings[mode] * 60);
  };

  const switchMode = (newMode: Mode) => {
    if (isActive) return; // [FIX #4] Prevent switching if running
    setMode(newMode);
    setTimeLeft(settings[newMode] * 60);
    initialTimeRef.current = settings[newMode] * 60;
  };

  const saveSettings = (newSettings: TimerSettings) => {
    setSettings(newSettings);
    localStorage.setItem("pomodoro_settings", JSON.stringify(newSettings));

    // If not active, update current timer display immediately
    if (!isActive) {
      setTimeLeft(newSettings[mode] * 60);
      initialTimeRef.current = newSettings[mode] * 60;
    }
    setIsSettingsOpen(false);
  };

  const formatTime = (seconds: number) => {
    const m = Math.floor(seconds / 60);
    const s = seconds % 60;
    return `${m}:${s < 10 ? "0" : ""}${s}`;
  };

  // --- VISUALS ---
  const radius = 120;
  const circumference = 2 * Math.PI * radius;
  const totalTime = settings[mode] * 60;
  const progress = ((totalTime - timeLeft) / totalTime) * circumference;

  const sessionsCount =
    todaySessions?.filter((s) => s.type === "work").length || 0;
  const totalFocusMinutes =
    todaySessions
      ?.filter((s) => s.type === "work")
      .reduce((acc, curr) => acc + curr.durationMinutes, 0) || 0;

  return (
    // [FIX #3] h-screen overflow-hidden to prevent scrollbars
    <div
      className={`h-screen w-full overflow-hidden transition-colors duration-700 flex items-center justify-center relative ${
        isZenMode ? "bg-gray-950" : "bg-[#F3F4F6]"
      }`}
    >
      {/* Ambient Background */}
      {!isZenMode && (
        <>
          <div
            className={`absolute top-[-10%] left-[-10%] w-[600px] h-[600px] rounded-full mix-blend-multiply filter blur-[120px] opacity-40 animate-blob ${MODE_CONFIG[mode].bg}`}
          ></div>
          <div
            className={`absolute bottom-[-10%] right-[-10%] w-[600px] h-[600px] rounded-full mix-blend-multiply filter blur-[120px] opacity-40 animate-blob animation-delay-2000 ${MODE_CONFIG[mode].bg}`}
          ></div>
        </>
      )}

      <div
        className={`w-full max-w-6xl grid grid-cols-1 lg:grid-cols-3 gap-6 p-6 z-10 transition-all duration-500 ${
          isZenMode ? "lg:grid-cols-1 place-items-center" : ""
        }`}
      >
        {/* --- MAIN TIMER CARD --- */}
        <motion.div
          layout
          className={`
                relative flex flex-col items-center justify-center 
                bg-white/80 backdrop-blur-2xl rounded-[40px] shadow-2xl border border-white/60
                transition-all duration-500 ease-out
                ${
                  isZenMode
                    ? "w-[500px] h-[600px] bg-black/40 border-white/10 text-white shadow-none"
                    : "lg:col-span-2 h-[600px] w-full"
                }
            `}
        >
          {/* Top Controls */}
          <div className="absolute top-8 right-8 flex gap-3">
            <button
              onClick={() => setIsSettingsOpen(true)}
              className={`p-3 rounded-full transition-colors ${
                isZenMode
                  ? "bg-white/10 hover:bg-white/20 text-white"
                  : "bg-gray-100 hover:bg-gray-200 text-gray-500"
              }`}
              title="Settings"
            >
              <Settings2 size={20} />
            </button>
            <button
              onClick={() => setIsZenMode(!isZenMode)}
              className={`p-3 rounded-full transition-colors ${
                isZenMode
                  ? "bg-white/10 hover:bg-white/20 text-white"
                  : "bg-gray-100 hover:bg-gray-200 text-gray-500"
              }`}
              title={isZenMode ? "Exit Zen" : "Enter Zen"}
            >
              {isZenMode ? <Minimize2 size={20} /> : <Maximize2 size={20} />}
            </button>
          </div>

          {/* Mode Switcher */}
          <div
            className={`flex gap-2 p-1.5 rounded-2xl mb-12 transition-colors ${
              isZenMode ? "bg-white/10" : "bg-gray-100"
            }`}
          >
            {(Object.keys(MODE_CONFIG) as Array<keyof typeof MODE_CONFIG>).map(
              (m) => (
                <button
                  key={m}
                  onClick={() => switchMode(m)}
                  disabled={isActive} // [FIX #4] Disable switching while running
                  className={`flex items-center gap-2 px-6 py-3 rounded-xl text-sm font-bold transition-all duration-300 ${
                    mode === m
                      ? `bg-white shadow-sm ${MODE_CONFIG[m].color} scale-100`
                      : `text-gray-400 ${
                          isActive
                            ? "opacity-50 cursor-not-allowed"
                            : "hover:text-gray-600"
                        } ${
                          isZenMode && !isActive ? "hover:text-gray-300" : ""
                        }`
                  }`}
                >
                  {m === "work" && <Brain size={18} />}
                  {m === "short" && <Coffee size={18} />}
                  {m === "long" && <Armchair size={18} />}
                  <span className="hidden sm:inline">
                    {MODE_CONFIG[m].label}
                  </span>
                </button>
              )
            )}
          </div>

          {/* Clock Circle */}
          <div className="relative mb-14 group">
            {/* Glow Effect */}
            <div
              className={`absolute inset-0 rounded-full blur-3xl opacity-20 bg-gradient-to-tr ${
                MODE_CONFIG[mode].gradient
              } transition-opacity duration-700 ${
                isActive ? "opacity-50" : ""
              }`}
            ></div>

            <svg className="w-80 h-80 transform -rotate-90 drop-shadow-2xl">
              <circle
                cx="160"
                cy="160"
                r={radius}
                stroke="currentColor"
                strokeWidth="8"
                fill="transparent"
                className={isZenMode ? "text-white/5" : "text-gray-100"}
              />
              <circle
                cx="160"
                cy="160"
                r={radius}
                stroke="url(#gradient)"
                strokeWidth="8"
                fill="transparent"
                strokeDasharray={circumference}
                strokeDashoffset={-progress}
                strokeLinecap="round"
                className="transition-all duration-1000 ease-linear"
              />
              <defs>
                <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%">
                  <stop
                    offset="0%"
                    stopColor={
                      mode === "work"
                        ? "#6366f1"
                        : mode === "short"
                        ? "#14b8a6"
                        : "#3b82f6"
                    }
                  />
                  <stop
                    offset="100%"
                    stopColor={
                      mode === "work"
                        ? "#a855f7"
                        : mode === "short"
                        ? "#10b981"
                        : "#06b6d4"
                    }
                  />
                </linearGradient>
              </defs>
            </svg>

            {/* [FIX #1] Absolute Centering for Time */}
            <div className="absolute inset-0 flex flex-col items-center justify-center z-10">
              <span
                className={`text-8xl font-bold tracking-tighter font-mono tabular-nums ${
                  isZenMode ? "text-white" : "text-gray-800"
                }`}
              >
                {formatTime(timeLeft)}
              </span>
              <span
                className={`text-sm font-bold uppercase tracking-[0.2em] mt-2 ${
                  isZenMode ? "text-white/40" : "text-gray-400"
                }`}
              >
                {isActive ? "Running" : "Paused"}
              </span>
            </div>
          </div>

          {/* Controls */}
          <div className="flex items-center gap-4">
            {/* [FIX #5] Stop & Save Button */}
            <button
              onClick={handleStopAndSave}
              disabled={!isActive && timeLeft === settings[mode] * 60}
              className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all duration-300 ${
                !isActive && timeLeft === settings[mode] * 60
                  ? "opacity-0 scale-0 pointer-events-none"
                  : isZenMode
                  ? "bg-white/10 text-red-400 hover:bg-white/20"
                  : "bg-red-50 text-red-500 hover:bg-red-100"
              }`}
              title="Stop & Save"
            >
              <Save size={22} />
            </button>

            <button
              onClick={toggleTimer}
              className={`w-24 h-24 rounded-[2rem] flex items-center justify-center text-white shadow-xl hover:shadow-2xl hover:scale-105 active:scale-95 transition-all bg-gradient-to-tr ${MODE_CONFIG[mode].gradient}`}
            >
              {isActive ? (
                <Pause size={36} fill="currentColor" />
              ) : (
                <Play size={36} fill="currentColor" className="ml-1" />
              )}
            </button>

            <button
              onClick={resetTimer}
              className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all hover:rotate-180 duration-500 ${
                isZenMode
                  ? "bg-white/10 text-white hover:bg-white/20"
                  : "bg-gray-100 text-gray-500 hover:bg-gray-200 hover:text-gray-700"
              }`}
              title="Reset"
            >
              <RotateCcw size={22} />
            </button>
          </div>
        </motion.div>

        {/* --- RIGHT: STATS (Hidden in Zen) --- */}
        {!isZenMode && (
          <div className="flex flex-col gap-6 h-[600px]">
            {/* Progress Cards */}
            <div className="bg-white/80 backdrop-blur-xl rounded-[30px] shadow-lg border border-white/50 p-8 flex-shrink-0">
              <h3 className="text-lg font-bold text-gray-800 mb-6 flex items-center gap-2">
                <Timer size={20} className="text-indigo-500" /> Today&apos;s
                Progress
              </h3>
              <div className="space-y-5">
                <div className="bg-indigo-50/50 p-5 rounded-2xl border border-indigo-100">
                  <div className="flex justify-between items-end mb-2">
                    <span className="text-4xl font-bold text-indigo-600">
                      {sessionsCount}
                    </span>
                    <span className="text-xs font-bold text-indigo-400 uppercase mb-1">
                      Sessions
                    </span>
                  </div>
                </div>

                <div className="bg-teal-50/50 p-5 rounded-2xl border border-teal-100">
                  <div className="flex justify-between items-end mb-2">
                    <span className="text-4xl font-bold text-teal-600">
                      {(totalFocusMinutes / 60).toFixed(1)}
                    </span>
                    <span className="text-xs font-bold text-teal-400 uppercase mb-1">
                      Hours
                    </span>
                  </div>
                </div>
              </div>
            </div>

            {/* History */}
            <div className="flex-1 bg-white/80 backdrop-blur-xl rounded-[30px] shadow-lg border border-white/50 p-8 flex flex-col overflow-hidden">
              <h3 className="text-lg font-bold text-gray-800 mb-4 flex items-center gap-2">
                <History size={20} className="text-gray-400" /> Recent Activity
              </h3>

              <div className="flex-1 overflow-y-auto custom-scrollbar pr-2 -mr-2 space-y-3">
                {todaySessions && todaySessions.length > 0 ? (
                  [...todaySessions].reverse().map((session) => (
                    <div
                      key={session.id}
                      className="flex items-center justify-between p-4 bg-white rounded-2xl border border-gray-50 shadow-sm hover:shadow-md transition-shadow"
                    >
                      <div className="flex items-center gap-3">
                        <div
                          className={`p-2 rounded-full ${
                            session.type === "work"
                              ? "bg-indigo-100 text-indigo-600"
                              : "bg-teal-100 text-teal-600"
                          }`}
                        >
                          {session.type === "work" ? (
                            <Brain size={14} />
                          ) : (
                            <Coffee size={14} />
                          )}
                        </div>
                        <div>
                          <p className="text-sm font-bold text-gray-700">
                            {session.type === "work" ? "Focus" : "Break"}
                          </p>
                          <p className="text-[10px] text-gray-400 font-medium">
                            {format(new Date(session.completedAt), "h:mm a")}
                          </p>
                        </div>
                      </div>
                      <span className="text-sm font-bold text-gray-600">
                        {session.durationMinutes}m
                      </span>
                    </div>
                  ))
                ) : (
                  <div className="h-full flex flex-col items-center justify-center text-gray-300 text-sm">
                    <p>No sessions yet.</p>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </div>

      {/* --- [FIX #6] SETTINGS MODAL --- */}
      {isSettingsOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm">
          <div className="bg-white rounded-3xl shadow-2xl w-96 p-8 animate-in fade-in zoom-in-95 duration-200">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-xl font-bold text-gray-800">
                Timer Settings
              </h2>
              <button
                onClick={() => setIsSettingsOpen(false)}
                className="p-2 hover:bg-gray-100 rounded-full transition-colors"
              >
                <X size={20} className="text-gray-500" />
              </button>
            </div>

            <div className="space-y-5">
              <div>
                <label className="block text-sm font-medium text-gray-500 mb-2">
                  Deep Focus (minutes)
                </label>
                <input
                  type="number"
                  value={settings.work}
                  onChange={(e) =>
                    setSettings({ ...settings, work: Number(e.target.value) })
                  }
                  className="w-full bg-gray-50 border border-gray-200 rounded-xl px-4 py-3 text-gray-800 font-medium focus:outline-none focus:ring-2 focus:ring-indigo-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-500 mb-2">
                  Short Break (minutes)
                </label>
                <input
                  type="number"
                  value={settings.short}
                  onChange={(e) =>
                    setSettings({ ...settings, short: Number(e.target.value) })
                  }
                  className="w-full bg-gray-50 border border-gray-200 rounded-xl px-4 py-3 text-gray-800 font-medium focus:outline-none focus:ring-2 focus:ring-teal-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-500 mb-2">
                  Long Break (minutes)
                </label>
                <input
                  type="number"
                  value={settings.long}
                  onChange={(e) =>
                    setSettings({ ...settings, long: Number(e.target.value) })
                  }
                  className="w-full bg-gray-50 border border-gray-200 rounded-xl px-4 py-3 text-gray-800 font-medium focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
            </div>

            <div className="mt-8">
              <button
                onClick={() => saveSettings(settings)}
                className="w-full bg-black text-white py-3.5 rounded-xl font-bold hover:bg-gray-900 transition-all shadow-lg hover:shadow-xl"
              >
                Save Changes
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
